

.images{
    position: relative;
    float: left;
    width:  250px;
    height: 200px;
    background-position: 50% 50%;
    background-repeat:   no-repeat;
    background-size:     cover;
    border-radius: 20px;
    cursor: pointer;
}

.date-tab{
    position: relative;
    float:center; 
    clear: left;

    
}

.img-div{
    float:left;
}

.tab-div{
    position: relative;
 
  transition: top ease 0.1s;
 
  top: 15px;  

}

.tab-div:hover{
    top: 50px;
}
body{
    background-color: LightCyan;
}